<template>
<CRow>
    <CCol col="12" class="mb-5">
        <CRow>
            <CCol col="8">
                <h3 class="font-weight-bold text-uppercase mb-1">Users Formulir</h3>
                <p class="text-primary text-sm-left mb-0">{{$options.identitas[0].instansi}}</p>
                <p class="text-primary text-sm-left">{{$options.identitas[0].provinsi}}</p>
            </CCol>
            <CCol>
                <div class="text-right mt-5 mr-5">
                    <router-link :to="{ path:'/settings/users'}" class="btn btn-primary text-white">Halaman Users</router-link>
                </div>
            </CCol>
        </CRow>
    </CCol>
    <CCol col="12">
        <div class="wrapperModul p-3">
            <CRow>
                <CCol sm="2">
                    <CInput label="Nama" v-model.trim="$v.user.name.$model" :isValid="$v.user.name.$dirty ? !$v.user.name.$error : null" placeholder="Ketik Nama" />
                    <pre>{{user}}</pre>
                </CCol>
                <CCol sm="2">
                    <CInput label="Email" v-model.trim="$v.user.email.$model" :isValid="$v.user.email.$dirty ? !$v.user.email.$error : null" placeholder="Ketik Email" >
                        <template>
                            <pre>ad</pre>
                        </template>
                    </CInput>
                </CCol>
                <CCol sm="2" v-if="$route.params.type === 'tambah'">
                    <CInput type="password" label="password" v-model.trim="$v.user.password.$model" :isValid="$v.user.password.$dirty ? !$v.user.password.$error : null" placeholder="Ketik Password" />
                </CCol>
                <CCol sm="2">
                    <CSelect :options="optionsInstansi" :value.sync="$v.user.instansi_id.$model" :lazy="true" :isValid="$v.user.instansi_id.$dirty ? !$v.user.instansi_id.$error : null" label="instansi" placeholder="Instansi" />
                </CCol>
                <CCol sm="1">
                    <CSelect :options="optionsRoles" :value.sync="$v.user.role_id.$model" :lazy="true" :isValid="$v.user.role_id.$dirty ? !$v.user.role_id.$error : null" label="Role" placeholder="Role" />
                </CCol>
                <CCol sm="3">
                    <CSwitch class="mr-1 mt-4 pt-1" color="primary" :checked.sync="user.active" v-model="user.active" />
                </CCol>
            </CRow>
            <CRow>
                <CCol col="12">
                    <CButton color="primary" class="text-white" @click="ToDatabase()">{{type}}</CButton>
                </CCol>
            </CRow>
        </div>
    </CCol>
</CRow>
</template>

<script>
import identitas from '@/containers/_identitas'
import tableta from '@/views/table/table'
import {
    required,
    email
} from 'vuelidate/lib/validators'
const axios = require('axios');
export default {
    identitas,
    components: {
        tableta
    },
    data() {
        return {
            user: {
                id: null,
                instansi_id: null,
                role_id: null,
                email: null,
                password: null,
                active: true,
                name: null,
            },
            instansi: [],
            roles: [],
            type: this.$route.params.type === 'edit' ? "update" : "insert",
        }
    },
    computed: {
        optionsInstansi() {
            return this.instansi.map((header, index) => {
                return {
                    label: header.instansi,
                    value: header.instansi_id,
                };
            });
        },
        optionsRoles() {
            return this.roles.map((header, index) => {
                return {
                    label: header.role,
                    value: header.role_id,
                };
            });
        }

    },
    validations: {
        user: {
            instansi_id: {
                required
            },
            role_id: {
                required
            },
            email: {
                required,
                email
            },
            password: {
                required
            },
            name: {
                required
            },

        }
    },
    created() {
        this.getData()
        this.getInstansi()
        this.getRoles()
    },
    methods: {
        getData() {
            axios.post(this.$options.identitas[0].url + '/users?type=dataById', {
                    id: this.$route.params.id
                })
                .then((e) => {
                    e.data.code === "404" ? this.$router.push({
                        path: '/pages/404'
                    }) : (this.user = e.data.data)

                })
                .catch(function (error) {
                    // handle error
                    console.log(error);
                })
        },
        getInstansi() {
            axios.post(this.$options.identitas[0].url + '/instansi?type=dataByAll', {
                    id: this.$route.params.id
                })
                .then((e) => {
                    this.instansi = e.data.data

                })
                .catch(function (error) {
                    // handle error
                    console.log(error);
                })
        },
        getRoles() {
            axios.post(this.$options.identitas[0].url + '/roles?type=dataByAll', {
                    id: this.$route.params.id
                })
                .then((e) => {
                    this.roles = e.data.data

                })
                .catch(function (error) {
                    // handle error
                    console.log(error);
                })
        },
        ToDatabase() {
            this.$v.$touch()
            if (!this.$v.$invalid) {
                axios.post(this.$options.identitas[0].url + '/users?type=' + this.type, {
                        data: this.user
                    })
                    .then((e) => {
                        this.$notify({
                            group: 'foo',
                            title: 'Information',
                            text: this.$route.params.type === 'edit' ? "Data Berhasil Di Ubah" : "Data Berhasil Di Tambahkan",
                        });

                        this.$router.push({
                            path: '/settings/users'
                        })
                    })
                    .catch(function (error) {
                        // handle error
                        console.log(error);
                    })
            }
        },
    }

}
</script>

<style>
.wrapperModul {
    border: 1px solid #dfdfdf;
    border-radius: 5px;

}

.float {
    position: fixed;
    width: 120px;
    bottom: 80px;
    right: 40px;
    color: #FFF;
    text-align: center;
    box-shadow: 2px 2px 3px #999;
}
</style>
